  text: '1898年，美国第一个篮球组织国家篮球联盟（NBL，1898-1904）成立，但当时篮球规则还不完善，组织机构也不健全，经过几个赛季后，该组织就名存实亡了。\n' +
    '\n' +
    '1946年6月6日，全美篮球协会（BAA）成立；1949年8月3日，BAA与国家篮球联盟（NBL）合并，改名“国家篮球协会”（NBA）。\n' +
    '\n' +
    '非白人球员\n' +
    '在成立初期，NBA的球员和教练全部由白人组成。1947年，美籍日裔的三阪亘被纽约尼克斯首轮选中，成为了NBA历史上首位非白人球员，同时他也是第一个加盟NBA的亚裔球员[4]。不过三阪亘在1947-48赛季仅仅为纽约尼克斯队出场三次贡献了七分后就被裁掉了。1950年，波士顿凯尔特人在第二轮选中了来自杜肯大学的黑人球员扎克·库珀（Charles Cooper），这是NBA历史上第一位入选的黑人[5]，同年一起被选中的还有“糖水”纳撒尼尔·克利夫顿（纽约尼克斯队）和厄尔·洛依德（华盛顿首都队），克利福顿在三人当中最先获得了球队合同[6]。1950年10月31日，劳埃德在华盛顿首都队与罗彻斯特皇家队的比赛中登场，黑人球员首次踏上了NBA赛场[7]。尽管当时美国的种族歧视仍然相当严重，不过随着比尔·拉塞尔（Bill Russell）、威尔特·张伯伦（Wilt Chamberlain）等人在球场上不断创造佳绩，黑人球员逐渐被球迷们接受并成为了NBA的主角。进入90年代后，黑人球员在数量上开始超越白人球员。2006-07赛季，黑人球员占NBA所有球员的比率已经达到75%，黑人主教练亦占NBA所有主教练的40%[8]。\n' +
    '\n' +
    '1966年，比尔·拉塞尔成为NBA首位黑人主教练，他作为球员兼教练带领波士顿凯尔特人获得了1968和1969年度的总冠军；而NBA亦成为了第一个拥有黑人主教练的职业体育联盟。NBA的首个黑人总经理和球队老板分别出现在1972年和2002年。\n' +
    '\n' +
    '标志\n' +
    'NBA的标志是由一位纽约平面设计师阿兰·西格尔（Alan Siegel）所设计，并在1969年首次出现在NBA官方的宣传资料上。不过NBA官方没有正式公布过标志的设计者。标志的图案是一名侧身控球的篮球员的剪影；整个标志由红、白、蓝三种颜色构成，这和MLB（美国职业棒球大联盟）的配色一样，西格尔解释说是为了“在两个联盟的标志之间营造一种视觉上的和谐”。很多人认为标志上的人物原型就是前洛杉矶湖人的明星球员杰里·威斯特（Jerry West），而西格尔也曾透露这一消息；不过NBA官方一直拒绝承认，并表示标志上的人物形象是一个“谜团”，因为NBA“没有任何关于标志设计者的确切记录”。[9]\n' +
    '\n' +
    '1940年代：BAA以及NBL\n' +
    '1946年，美国东北部及中西部一些大型体育球馆的老板在纽约商讨成立一个篮球组织的事宜；不过这些球馆的老板对篮球并不是十分的感兴趣，他们原本就拥有冰球队伍，因此老板们最初的目的只是为了使他们的球馆在冰球休赛期间得到更充分的利用。\n' +
    '\n' +
    '1946年6月11日，包括著名的纽约麦迪逊花园广场在内的1家球馆老板们联合成立了全美篮球协会（BAA）。美国冰球联盟（AHL）的主席莫里斯·波杜夫担任了NBA（BAA）的第一任主席[10][11]。\n' +
    '\n' +
    '1946年11月1日，NBA（BAA）历史上的第一场正式比赛在加拿大多伦多的枫叶花园球场举行，由多伦多爱斯基摩人主场迎战纽约尼克斯，共有7,090名观众涌入球馆观看了这场赛事，结果纽约以68-66取得了胜利[10]。费城勇士获得了第一个NBA（BAA）总冠军。当时BAA的竞争对手有“美国篮球联盟”（ABL）、“国家篮球联盟”（NBL）等，BAA最先尝试了在大城市的大球馆进行比赛。\n' +
    '\n' +
    '1949年8月3日，BAA同意与当时的竞争对手、主要位于中西部的国家篮球联盟（NBL）合并，组成了一个拥有17支球队的大联盟，并命名为“国家篮球协会”（NBA）[12]。不过新协会成立的第二个赛季，球队数量就缩减到11支；随着小球队的相继倒闭，1954年NBA只剩了下8支球队。这8支球队也一直保留到现在（亚特兰大老鹰、波士顿凯尔特人、底特律活塞、金州勇士，洛杉矶湖人、纽约尼克斯、费城76人以及萨克拉门托国王），当然很多球队的主场已经变迁，为了更好的发展，那些深处偏僻小城的球队都相继搬到了大城市。譬如老鹰队由三城先后迁往密尔沃基、圣路易斯和亚特兰大，活塞队则从韦恩堡搬到底特律，湖人由明尼阿波利斯迁徙到洛杉矶，国王队从罗彻斯特先后移至辛辛那提和萨克拉门托。在1961-62赛季芝加哥包装工人队加入联盟之前，NBA一直由这8支球队来维持场面。并且在1967-68赛季之前，NBA球队数量一直停留在10支以下。\n' +
    '\n' +
    '1950年代：24秒钟装置投入使用，湖人王朝，凯尔特人王朝\n' +
    '\n' +
    '1950年代囊括4座总冠军的明尼阿波利斯湖人\n' +
    '为了鼓励进攻，1954年NBA迎来了第一次历史性的改革:24秒钟装置。当时由于比赛中没有进攻时间限制，控球一方往往都不轻易投篮以免失去进攻机会，防守一方唯有采用犯规来夺回控球权，因此比赛基本都是在罚球线上度过的，不仅节奏缓慢，得分也很低。为了改变这种状况，使比赛节奏加快并流畅，条例规定，如果控球一方在24秒内未能把球接触到篮筐将会被判违例并失去控球权。NBA除了制定了24秒进攻时间的新规则外，同时还将球队单节犯规次数限制为6次。改革的效果是明显的，实行24秒新规则的第一个赛季，NBA球队的平均得分达到了93.1分，比前个赛季多了13.6分，波士顿凯尔特人更成为了首支赛季平均得分过百的球队[13]。\n' +
    '\n' +
    '50年代初，在中锋乔治·迈肯的带领下，明尼阿波利斯湖人在1949到1954六年间五次夺得总冠军，缔造了NBA历史上第一个湖人王朝。1955-56赛季，波士顿凯尔特人得到了新秀中锋比尔·拉塞尔，在跑轰传奇教练阿诺·奥尔巴赫的带领下，波士顿凯尔特人拿到了接下来13年中11个总冠军，包括1959到1966年的八连霸。比尔·拉塞尔一生最伟大的对手威尔特·张伯伦也在1959年加入到NBA，与比尔·拉塞尔疯狂的夺冠记录不同，张伯伦最闪耀的是他的个人数据，包括单场最高分（100分），单场最高篮板（55个）都由他来保持。联盟生态方面 ，常规赛表现东部赛区总体比西部赛区略强，总冠军则是五五波。\n' +
    '\n' +
    '1953-54赛季，电视台首次转播NBA赛事，当时的杜蒙特电视台与NBA签订了一份39,000美元的合同，在当个赛季播出了13场NBA比赛。\n' +
    '\n' +
    '1960年代：联盟扩张、ABA的挑战\n' +
    '\n' +
    '1960年代囊括9座总冠军的波士顿凯尔特人\n' +
    '1960年，湖人队从明尼阿波利斯迁到洛杉矶，成为NBA首支西部球队，此后费城勇士（现金州勇士）、锡拉丘兹民族（现费城76人）、圣路易老鹰（现亚特兰大老鹰队）亦分别迁居。芝加哥包装工（现华盛顿奇才）于1961加入，成为第9支NBA球队。1966-68年间又增加了5队，分别为芝加哥公牛、西雅图超音速（现俄克拉何马雷霆）、圣迭戈火箭（现休斯敦火箭）、密尔沃基雄鹿及菲尼克斯太阳。1967年，NBA受到了“美国篮球协会”（ABA，1967-1976）的强力挑战。1967年成立的ABA采用非传统的红、白、蓝三色球，设立三分线，使用30秒的进攻时间；这个新兴的职业联盟更强调进攻，以开放性以及自由度来吸引球迷和球星。而ABA的开放性、娱乐性以及自由度是当时NBA无法相比的。由于当时NBA不允许球员未毕业的大学球员直接进入联盟，不少极具天赋的年轻人选择了ABA。NBA则更注重大学里的新星，传奇中锋“天勾”卡里姆·阿卜杜勒-贾巴尔（Kareem Abdul-Jabbar）在1969年加入联盟。联盟生态方面则是明显东强西弱，包括60年代全部的冠军都由东部包办。\n' +
    '\n' +
    '\n' +
    '1966年，波士顿凯尔特人的比尔·拉塞尔与费城76人的威尔特·张伯伦\n' +
