    '\n' +
    '拉里·伯德带领波士顿凯尔特人5次杀入总决赛3次夺冠，魔术师约翰逊联同卡里姆·阿卜杜勒-贾巴尔将洛杉矶湖人9次送进总决赛并捧得5次奖杯。这时东部因波士顿凯尔特人、费城76人、底特律活塞、密瓦尔基雄鹿等强队，总体实力是略高于包含洛杉矶湖人的西部，他们两个共同缔造NBA的80年代辉煌时代。\n' +
    '\n' +
