    '\n' +
    '自1980年起，随着达拉斯独行侠、夏洛特黄蜂、迈阿密热火、明尼苏达森林狼、奥兰多魔术等球队相继加入，至1989-90赛季，NBA已经增加到27支球队。\n' +
    '\n' +
    '1990年代：全球化，WNBA创立\n' +
    '进入90年代，NBA开始迈向国际化。1992年巴塞罗那奥运会，美国篮协首次派出由NBA球星组成的梦之队参赛，包括迈克尔·乔丹、拉里·伯德、魔术师约翰逊等明星。并以平均每场胜出44分的巨大优势获得了冠军[16]，这支由NBA顶级球星组成的梦之队让世界认识到了NBA的魅力。\n' +
    '\n' +
    '这时东部西部实力虽然各有千秋，不过东部可有由迈克尔·乔丹所领军的超级强队公牛，在91-93球季拿下三连冠，夸张的是，乔丹跑去打棒球之后复出，又在96-98这三年拿下三连霸，并创下当时NBA最高的72胜10负，其他东部强队只能感叹生不逢时。这纪录一直到2015-16年的金州勇士队创下73胜9负，才将之改写。\n' +
    '\n' +
    '为获得更多的商业利益，NBA把部分季前赛甚至常规赛安排在欧洲、亚太以及墨西哥等地举行，这些比赛亦称为NBA海外赛；1990-91赛季，NBA把菲尼克斯太阳与犹他爵士的两场常规赛的比赛场地安排在日本东京，这是NBA正式赛事首次在非美国及加拿大本地举行。同时全世界对NBA的关注随着电视转播而扩大，2006-07赛季，全球共有215个国家41种语言188家电视台转播NBA赛事或制作NBA相关节目[17]。\n' +
    '\n' +
    '随着NBA的影响扩大，越来越多的来自世界各地的球星纷纷加入到NBA。近年来，NBA为了吸引更多来自世界各地的优秀球员，将国际球员的限制逐渐放宽；原先国际球员一般都是先进入NCAA篮球联赛才能参加NBA选秀，现在已经可以直接参选了，NBA球探都亲自到各国联赛去挖掘有潜力的新星。很多国际球星在NBA闯出了名堂，例如来自尼日利亚的中锋1994年MVP阿基姆·奥拉朱旺（Hakeem Olajuwon）带领休斯敦火箭两次问鼎NBA总冠军，德国球星德克·诺维茨基（Dirk Nowitzki）把达拉斯独行侠带进2006年NBA总决赛并获得了2007年MVP以及2011年总冠军及FMVP，还有连续当选2005年和2006年MVP的加拿大控卫史蒂夫·纳什（Steve Nash），四次荣获最佳防守球员的萨伊中锋迪肯贝·穆托姆博（Dikembe Mutombo）等等。2006-07赛季NBA的国际球员达到60名，他们来自28个不同的国家[18]。\n' +
    '\n' +
    '1995年，多伦多猛龙与温哥华灰熊加入NBA，使得NBA的版图再次扩展至美国本土之外。\n' +
    '\n' +
    '1996年，NBA创办了国家女子篮球协会（WNBA）。\n' +
    '\n' +
    '1999年，NBA开办了自己的电视频道NBA-TV。\n' +
    '\n' +
    '2001年，NBA又创立了国家篮球协会发展联盟，即现在的NBA G联盟（G-League）。\n' +
    '\n' +
    '1990年代末：第一次封馆危机\n' +
    '1990年代末也爆发了一场劳资纠纷。1998年由于赛前劳资谈判彻底失败，劳方和资方拒绝任何往来，因此NBA进入全面性封馆。在205天后，劳资双方靠着多方面牵线进行许多密集会议，直到1999年1月25日劳资双方达成十年收益分配协议，1998-99赛季推迟到1999年2月5日才开始，当年的常规赛也由82场压缩到50场。圣安东尼奥马刺在该年夺下封馆后的NBA总冠军。\n' +
    '\n' +
    '第30支球队，新球试用\n' +
    '2004-05赛季，随着夏洛特山猫的加入，NBA球队达到了30支。\n' +
    '\n' +
    '在千禧年之后，很明显地变成了西强东弱的局面，举例来说09-10年的赛季，高达50胜的俄克拉何马雷霆竟然只能以老八之姿进入季后赛。由三巨头GDP马努·吉诺比利（Manu Ginóbili）、托尼·帕克（Tony Parker）、蒂姆·邓肯（Tim Duncan）的圣安东尼奥马刺，禅师菲尔·杰克逊、科比·布莱恩特、沙奎尔·奥尼尔的湖人，跟德克·诺维茨基所领军的达拉斯独行侠，中国状元姚明、特雷西·麦格雷迪领军的休斯敦火箭队则是在近代西部列强中较为人所知。\n' +
    '\n' +
    '2006年6月29日，NBA官方介绍了将在2006-07赛季投入使用的新球。这也是NBA35年来首次更换比赛用球，在NBA60年历史中也仅仅是第二次。由斯伯丁公司负责设计生产的新球使用新的合成纤维材料，他们称新球可以让球员抓的更紧，更有球感。事实上，很多球员对新球并不好感，抱怨新球干的时候太黏手，沾到汗水却又太滑。于是NBA官方不得不在2007年1月1日重新使用皮革制球。2006年，由于锐跑被阿迪达斯所并购，所以NBA的球衣供应商也由原本合作多年的锐跑改换为阿迪达斯。\n' +
    '\n' +
    '第二次封馆危机：联盟停摆\n' +
    '由于1999年制订十年劳资收益分配已到期，即将进行劳资谈判2011年7月1日，NBA执行长大卫·斯特恩（David Stern）再次宣布NBA进入封馆，其原因主要为球团认为球员工资飙升，使得球团无利可图，甚至造成联盟总亏损加倍，双方在劳资谈判过程中意见严重分歧，导致无法达成共识，进而导致NBA联盟第二次封馆的命运。\n' +
    '\n' +
    '2011年8月10日，进行第二场劳资会议，但由于资方和劳方又无法达成共识，其主要原因为资方提高球团豪华税、球员奢侈税的税收，球员工会拒绝再提高其他不必要税收，因此NBA执行长David Stern再次宣布取消季前热身赛两周，导致第二场会议不欢而散。\n' +
    '\n' +
    '2011年9月20日，进行第三场劳资会议，有15位NBA现役球员参与这次劳资谈判中，球员工会目的在于描述球员主观和客观的想法和意见，为争取球员最大权益，但是不为资方所理解，由于资方对于电视转播权益金和收益比例无法达成共识，且各球团老板各有分歧意见，球团工会全体气愤而退席，NBA执行长大卫·斯特恩再次宣布取消季前所有热身赛和常规赛10月~11月的赛程。\n' +
    '\n' +
    '2011年10月12日，球员工会召开商业会议，有部分球员提议另组新NBA联盟和解散球员公会，诉诸法律运动裁决法庭审判。\n' +
    '\n' +
    '2011年10月31日，进行第四场劳资会议，虽然双方以谈定电视转播权利金和收益比例，但最主要工资分配始终无法达成协定，资方的理想收益与劳方的理想收益相差甚多，球员工会坚持要占收益52.5％比例，但是资方坚持占50.5％比例为底限，最后分歧意见过大，NBA执行长大卫·斯特恩决定取消常规赛一半以上的赛程，球员工会则是决定解散公会，也已经向美国劳动仲裁机构提交诉讼。\n' +
    '\n' +
    '2011年11月26日，经过25天的劳资互不往来。于26日进行第五场劳资会议，再度聚集纽约集会，结果在超过15小时的协商后，双方终于达成初步协议。NBA执行长大卫·斯特恩表示：“新版劳资协议的产生，对于新球季在12月25日开打感到乐观”，2011-12年球季各队约有66场常规赛。\n' +
    '\n' +
    '联盟新气息\n' +
    '2011年11月30日，新版劳资协议达成，NBA联盟正式公开，球员工会确定同意，常规赛于12月25日圣诞节正式开打。\n' +
    '\n' +
    '2016年4月16日，NBA总裁萧华宣布自2017-18年球季起，联盟将开放各队在球衣左上方印上赞助商广告。这将是北美四大职业运动，首个正规赛球衣广告创举。球衣补丁计划目前仍有些许问题需要解决，例如球星代言冲突，此计划将进行三个球季，再对球迷反应与实际成效进行检讨。另外，贩售球衣部分，球团可自行选择，有广告赞助或无广告标志商品，卖给球迷。\n' +
    '\n' +
    '2017年9月24日，自1985年起，每年NBA总冠军会接受总统会见，该传统在2017年被打破[19]。2017年冠军金州勇士内部就是否前往白宫产生分歧，而特朗普推特上宣布"由于库里犹豫不决，所以取消邀请金州勇士前往白宫"。特朗普撤销邀请引发大量NBA球员抨击，NBA联盟总裁席佛也对此表示对此事件感到遗憾。NBA球员工会表达尊重双方意见，表示对此事件感到惋惜。\n' +
    '\n' +
    '2018年8月24日，NBA官方向各队球团与球团负责人与球团总经理与球团现任教练团提出一份备忘录，上面提到联盟将针对3项规则做出修改，进攻方在抢到进攻篮板后的进攻时间从原本的24秒缩短为14秒、简化快攻蓄意犯规的规则定义、扩大对恶意行为的定义（包括球员恶意语言谩骂），NBA赛事委员会将会在9月下旬召开会议以针对这3项规则进行投票，只要通过三分之二的门槛，新规则就将于新赛季正式施行，并且先NBA G联盟（NBA G League）测试。NBA球员工会也对NBA联盟联盟这三项规则修改提议表示尊重与关切。\n' +
    '\n' +
    '2020年3月12日，由于犹他爵士队中锋鲁迪·戈贝尔（Rudy Gobert）确诊新型冠状病毒（Covid-19），联盟宣布无限期停赛。6月4日，NBA联盟董事会正式通过22支球队复赛的提议，2019-20赛季剩余赛事于7月31日在奥兰多的迪士尼世界重启，称为NBA泡泡。达米姆·利拉德获得种子赛MVP奖项。在总决赛中，洛杉矶湖人队以4比2的比分战胜了迈阿密热火队。\n' +
    '\n' +
    '2020-21赛季，本赛季首次引进分区7-10名进行附加赛决定7和8种子。而西部的曼菲斯灰熊也成为历史上第一支以分区第9名拿到季后赛门票的球队。\n' +
    '\n' +
    '2021-22赛季，是NBA的第76个赛季，NBA官方评选出了75大球星。\n' +
    '\n' +
    '球队\n' +
    'Map of USA and Canada, NBA, zoom.svg\n' +
    '\n' +
    '波特兰开拓者犹他爵士丹佛掘金俄克拉何马雷霆明尼苏达森林狼洛杉矶快船洛杉矶湖人菲尼克斯太阳萨克拉门托国王金州勇士达拉斯独行侠休斯敦火箭圣安东尼奥马刺新奥尔良鹈鹕孟菲斯灰熊密尔沃基雄鹿芝加哥公牛印第安纳步行者克利夫兰骑士底特律活塞多伦多猛龙波士顿凯尔特人纽约尼克斯布鲁克林篮网费城76人华盛顿奇才夏洛特黄蜂迈阿密热火奥兰多魔术亚特兰大老鹰\n' +
    'NBA由成立之初的11队发展到现在的30队，当中经历了许多新球队的建立，老球队的变迁或是倒闭。70年历史中波士顿凯尔特人及洛杉矶湖人是最成功的球队，他们一共各获得了17个NBA总冠军。2012年最值钱的球队是洛杉矶湖人，市值9亿美元。2014年，微软前执行长鲍尔默将以20亿美元（约台币600亿元）天价买下快船，也写下NBA球队转手价格新纪录。[20]\n' +
    '\n' +
    '现在NBA球队多数是以球队所在城市名称作为球队名，部分以州份名称作为球队名的球队有明尼苏达森林狼、犹他爵士、印第安纳步行者和金州勇士（使用了加利福尼亚州的别名金州）；而布鲁克林篮网队是唯一使用区名的队伍，其队名来自纽约市布鲁克林区。目前洛杉矶、纽约两城市各拥有两只球队。\n' +
    '\n' +
    'NBA目前采用的是2004-05赛季划分的区域，分为东西2个联盟，每个联盟各有3个赛区，每个赛区各有5队。\n' +
