    'AT&T Center[32]\t1967年*\t1976年\n' +
    '曾经更名的球队\n' +
    '底特律活塞（前韦恩堡活塞），洛杉矶湖人（前明尼阿波利斯湖人），萨克拉门托国王（前罗彻斯特皇家）1948年由国家篮球联赛加盟BAA（NBA）。\n' +
    '费城76人（前锡拉丘兹民族），亚特兰大老鹰（前三城黑鹰）在1949年BAA和NBL合并的时候由NBL并入。\n' +
    '俄克拉何马城雷霆前身原是西雅图超音速，因试图于西雅图新建球场被拒后，索性将球队迁移至俄克拉荷马城并更名为俄克拉何马城雷霆，而NBA总部已和西雅图官员达成协议，保留超音速队名及队徽颜色，希望未来新增或有球队迁至西雅图时，能继续沿用。\n' +
    '印第安纳步行者、圣安东尼奥马刺、布鲁克林篮网（前纽约/新泽西篮网）和丹佛掘金，在1976年NBA兼并ABA的时候由ABA并入。\n' +
    '由于球队主场馆新奥尔良竞技馆受到卡特里娜飓风的严重破坏，新奥尔良黄蜂（现新奥尔良鹈鹕）于2005-06赛季及2006-07赛季的主场比赛是安排在俄克拉荷马市的福特中心进行[33]，且暂时更名为“新奥尔良/俄克拉荷马市黄蜂”。\n' +
    '夏洛特黄蜂于1988年成立，2002年牵至纽澳良改名新奥尔良黄蜂，2013年改名新奥尔良鹈鹕。\n' +
    '夏洛特山猫在2004年成立，在迈克尔·乔丹买下后，于2014年与纽澳良鹈鹕队达成协议，山猫队改名夏洛特黄蜂，并承接原属鹈鹕队的1988~2002年夏洛特黄蜂队史。\n' +
    '已经解散的球队\n' +
    '主条目：已解散的NBA球队列表\n' +
    '常规赛和全明星周末\n' +
    '主条目：NBA常规赛、NBA全明星周末和NBA赛季列表\n' +
    '夏季联赛（NBA Summer League）在选秀之后举行，一般在7月份，由新秀和3年以下球龄的球员参加，主要为考察新人而设，又称为“拉斯维加斯联赛”。9月末每队会开始夏季训练营，给球员热身，让教练评估球员表现，考察新人，确定赛季轮换12球员和3名备用球员的名单。之后队伍可以安排参加夏季联赛和季前赛。季前赛（Preseason）在每年的10月份举行，为常规赛前最后的热身赛。每支球队参加季前赛的球员名单基本与常规赛相同，不过主力上场的时间很少以避免受伤，主教练也借此机会来演练不同的阵容。夏季联赛和季前赛允许非NBA球队参加，而NBA还会把部分季前赛安排在海外进行。\n' +
    '\n' +
    '\n' +
    '波士顿凯尔特人与明尼苏达森林狼的比赛\n' +
    '每年10月的最后一周常规赛（Regular season）正式开始。在常规赛中，每支球队需要打满82场比赛，主客场各占一半，其中与同一赛区的4队要打4场，共16场；与同一个联盟（东部或西部）不同赛区的10队打3或4场，共36场；与不同联盟的球队打两场，共30场；一个赛季每支球队在自己的主场至少与其它29队进行一次交锋。2008年后，NBA是北美四大体育联盟唯一一个所有球队在常规赛全部碰面的联盟。常规赛的赛程紧凑，球队在每个星期有3到4场比赛，而且每个赛季都有不同程度的“背靠背作赛”（球队连续2天在不同的城市客场作战）、连续客场作赛等艰难赛程。除揭幕战外，圣诞节比赛也是常规赛的焦点赛事；NBA是北美四大体育联盟中唯一在圣诞节安排比赛的，通常是当前最具人气的球星之间的对垒，如2008-09赛季的圣诞节比赛由勒布朗·詹姆斯率领的克利夫兰骑士对阵德维恩·韦德领衔的迈阿密热火[34]。常规赛还设有每周东（西）部最有价值球员和每月东（西）部最有价值球员。\n' +
    '\n' +
    '次年2月，常规赛将会暂停1周来举行一年一度（1999年曾因劳资纠纷停办[35]）的NBA全明星赛（All-Star Game）。美国及加拿大当地球迷对全明星先发阵容进行投票，全世界球迷也可以通过互联网来投票。东西部各个位置得票最高的球员将获得首发，其余14名球员由各队教练投票选出，全明星教练则是由全明星赛两周前东西部成绩最好的球队教练担当（可以多任，但不能连任）。东西部获胜的一方表现最好的球员将获得全明星赛MVP。全明星赛前还有一系列的表演及活动来吸引球迷们的眼光，整个全明星周末的活动包括：\n' +
    '\n' +
    '新秀挑战赛，比赛的形式不定，历史上大多由应届新秀挑战二年级球员，也有应届新秀按东西部联盟对抗的情况，2012年则是由两位NBA名宿查尔斯·巴克利和奥尼尔在优秀的年轻球员中挑选各自属意的队员进行比赛。2015年改为NBA本土新秀对阵NBA外籍新秀。\n' +
    '技巧挑战赛，参赛者必须在最快的时间内完成运球，传球，投篮，上篮等一系列动作方能获胜，2015年改为两人同场对抗。\n' +
    '三分大赛，规定时间内在5个角度投中最多三分球者胜出，2015年调整为任意一个角度可以放5个加分球总得分由30分变成34分\n' +
    '扣篮大赛，参赛者尝试做出各种高难度、极具观赏性的扣篮动作，通常邀请历届扣篮大赛冠军作为评判，每人只有3次机会，否则，打出最低分30分\n' +
    '混合投篮赛，由一名现役NBA球员、一名现役WNBA球员和一名退役的NBA球星组成队伍，按要求在不同的投篮点投篮，完成最快组胜出\n' +
    '除此之外，在整个全明星周末期间，每天都有NBA嘉年华的演出。\n' +
    '\n' +
    '全明星周末后很快来到球员交易的最后期限，即赛季的第16个星期四。这一天之后，球队之间不能再交易球员。不过仍可解雇球员，或者跟自由球员签订十日合同[36]。\n' +
    '\n' +
    '四月份常规赛结束，紧接着进行季后赛。NBA官方会陆续颁发一些奖项来表扬常规赛表现出色的球员，以及评选最佳阵容，最佳防守阵容及最佳新秀阵容。\n' +
    '\n' +
    'NBA最有价值球员，是美国国家篮球协会从1955-1956年NBA赛季开始颁发的年度奖项，旨在表彰赛季中综合表现最优异的球员。\n' +
    'NBA最佳新秀，年度表现最好的新秀。\n' +
    'NBA最佳防守球员，年度最佳防守球员，多数由中锋获得。\n' +
    'NBA最佳第六人，年度最佳板凳球员，替补出场次数必需大于首发次数。\n' +
    'NBA进步最快球员，年度数据提升最多的球员。\n' +
    'NBA最佳教练，全联盟成绩最好的球队教练不一定能获此殊荣，一般由善于挖掘球队潜力，带领球队取得意想不到成绩的教练获得。\n' +
    'NBA最佳经理，由体育新闻杂志（Sporting News）颁发的非官方奖项，不过已得到广泛认可。\n' +
    'NBA体育精神奖，表扬公平竞赛的球员。\n' +
    '沃特·肯尼迪公民奖，表扬热心慈善公益活动的球员，由职业篮球作家协会颁发。\n' +
