    '附注：\n' +
    '巴尔的摩子弹队是唯一一支已经解散、获得过NBA总冠军的球队，这里所指的是1947年成立的巴尔的摩子弹队，并非现行华盛顿奇才的前身巴尔的摩子弹队(1963-1973)。\n' +
