    '劳资关系\n' +
    '参见：NBA劳资协议\n' +
    'NBA球员工会称作国家篮球球员协会（NBPA），由前波士顿凯尔特人球星鲍勃·库锡于1954年建立[35]，负责代表球员与球队老板进行谈判，并维护球员们的利益。工会执行主管是Michele Roberts，工会现任主席是来自波特兰开拓者的C·J·麦科勒姆。劳资协议（Collective Bargaining Agreement，简称CBA）是NBPA和NBA达成的一个合约，是所有球员合同的基础。\n' +
    '\n' +
    '1998年，由于NBPA拒绝NBA提出的将球员工资由占BRI的57%降到48%和设立硬工资帽的提议，NBA从7月1日起关闭球馆并停止一切商业活动，直到第二年的1月20日双方达成协议签订了新的CBA。这次长达7个月191天的停摆是NBA历史上最长的一次停止运作，1998-99赛季常规赛最终被压缩到50场，同时全明星周末亦停办；球员损失工资达到四亿美元，而球队及NBA联盟损失接近十亿美元[35]。\n' +
    '\n' +
    '而上一份劳资协议使用期限是2005-06赛季至2010-11赛季，2010-11赛季后双方在谈判过程中意见分歧很大，未能达成共识，因此造成停摆，使得2011-12赛季从82场被压缩到剩66场常规赛。\n' +
    '\n' +
    '最新的一份劳资协议是在2011年签订的，其中对奢侈税的征收变得更严厉，并采阶梯式上升，将在2014-15赛季实施。\n' +
    '\n' +
