    '10+\t1,219,590\t18,257,750\n' +
    '根据最新的劳资协议，自由球员与球队签订的合同最长年限为五年（使用拉里·伯德条款为六年），年薪涨幅最高为8%（使用拉里·伯德条款为10.5%）；年限超过四年（包括四年）的合同经过三个赛季后可以被延长，延长合同的第一年工资不得高于原合同最后一年的110.5%；合同里可以包含球队选项、球员选项以及球员提前终止合约选项中的其中一项或多项附加条款。劳资协议同时规定了球员工资的上下限。2007-08赛季在NBA效力十年以上的球员将至少可以得到1,219,590美元的年薪，而首次参加NBA的球员（包括新秀）的最低年薪是427,163美元；底薪每年上涨3.5%。球员可以得到的最高工资随着NBA球龄而不同，少于六年球龄为900万与工资帽的25%中的最大值，七到九年球龄为1100万与工资帽的30%中的最大值，十年以上为1400万与工资帽的35%中的最大值。同时NBA亦规定了球员最高工资不得少于上一年工资的105%，由于工资涨幅关系，球员可以拿到超过当年最高年薪上限的工资。如2007-08赛季全联盟薪水最高的凯文·加内特（波士顿凯尔特人）的工资是2200万美元，远远高于最高年薪上限的1838.8万美元[38]。\n' +
    '\n' +
    '第一轮新秀的新秀合同期限为四年（后两年可选择执行或放弃），球员在第二个赛季前可以选择执行第三年的合约或成为自由球员，如果执行了，在第三个赛季前还可以选择执行第四年的合约或成为自由球员。当新秀完成四年合同后，他将成为受限制自由球员。\n' +
    '\n' +
    '十日合同（10-day Contract）是赛季中后段球队为了应付由于伤病或其它原因缺乏人手的问题与自由球员签订的临时合约，期限为十天（或三场比赛，取较大值）。一支球队与一名球员在一个赛季内只能签订两次十日合同，如果第三次签约则必须把期限延长到赛季结束，同时球队的十日合同的数量不得超过伤病名单的人数。十日合同在每年1月5日后才允许签订。\n' +
    '\n' +
    '双向合同（Two-Way Contract）球队可以拥有最多两个双向合同球员。他们既可参与NBA比赛，亦可参与NBA G联盟赛事，但以G联盟为优先。每季最多可以参与45场NBA常规赛，但该球员不得参加季后赛。球队通常安排球双向合同球员前往姊妹球队训练及比赛，但是一些没G联盟球员则需与其他球队安排。。\n' +
    '\n' +
    '自由球员\n' +
    '自由球员（Free Agency）包括受限制和非受限制的，区别在于，当受限制自由球员（Restricted Free Agency）与新球队达成协议（新球队对球员只能提供邀请合同（Offer Sheet））的时候，如果球员原属球队在15天内开出同等价值的合同，那么将可以留住这名球员；非受限制自由球员则不受原属球队的任何约束。\n' +
    '\n' +
    '首轮新秀在完成四年的新秀合同后将自动成为受限制自由球员，而其他自由球员如果在NBA效力的时间不足三年的也是受限制的。不过，没有执行新秀合同第三或第四年选择条款的首轮新秀、或者被球队选中后放弃的新秀将成为非受限制自由球员。\n' +
    '\n' +
    '球队交易[36][37]\n' +
    '在NBA，工资上限以下的球队可以任意进行球员交换，不过鉴于NBA球队的总工资基本上都在工资帽以上，球队在交易过程中使用最多的就是交易球员特例（Traded Player Exception），另外还有底薪球员特例和伤残球员特例。按照交易球员特例，球队在即时交易（Simultaneous Trade）中转入的总年薪不得高于转出总年薪的125%+10万，可以涉及多名球员；而球队在非即时交易（Non-simultaneous Trade）中只能送出一名球员（交易筹码甚至可以不是球员），交易出现的合同差额将作为一个“交易补偿”，由转出高合同球员的球队获得，这个“补偿”可以用到同年或下一年的交易中，不过通常是跟选秀权搭配来使用，因为选秀权在交换过程中工资价值为零。NBA还规定连续两年的首轮选秀权不能同时送出。球员在交易过程中最多可以使用300万现金，但是现金的工资价值同样为零。\n' +
    '\n' +
    '球队工资帽及豪华税\n' +
    '参见：NBA工资帽\n' +
    '特殊条款\n' +
    '主条目：NBA特殊条款\n' +
    '特殊条款是针对工资帽而制定的特例。包括有拉里·伯德条款、中产阶级条款、一百万特例、底薪特例等，总工资超过工资上限的球队可以使用这些条款来与自由球员签约。不过球队对这些条款的运用也有限制，如中产阶级条款每个赛季只能对一名球员使用，而一百万特例不能连续两个赛季采用。\n' +
    '\n' +
    '球员和教练\n' +
    'NBA球员大多数来自美国大学篮球联赛（包括NCAA、NAIA、NJCAA等），当然也有直接从高中加盟NBA的，不过新的劳资协议对高中球员作了限制，要求必需在高中毕业一年后才能参加选秀[36]。近年来随着NBA在国际市场的不断开拓，参加NBA的国际球员也日益增多；海外球员一般来自各国的篮球联赛，他们可以参加选秀或者直接跟球队签约。球员比例方面，黑人球员是NBA的主导，一直保持在70%以上。\n' +
    '\n' +
    '\n' +
    'NBA教练菲尔·杰克逊在与裁判争论\n' +
    'NBA球衣及服装由耐克统一赞助，不过球鞋是一个例外——球员可以拥有自己的球鞋赞助商。另外NBA制定有球员服装令（Player Dress Code）[39]，球员在参加联盟及球队的非比赛及训练活动中必须穿“商务休闲装”。球员在比赛场上的不文明行为（辱骂、打人等）都会受到NBA的罚款甚至禁赛处罚；NBA还规定了球员在比赛期间不得冲上看台。\n' +
    '\n' +
    '与球员不同，NBA主教练尽管没有受到服装令的约束，但他们在每场比赛都穿上整齐的西服在场边指挥，颇显绅士风度。NBA对主教练的一条重要规定是，在比赛进行过程中主教练不得离开替补席周围28码的范围，或越过球场边线[40]，包括和裁判争论的时候，否则就会被判技术犯规。\n' +
    '\n' +
    'NBA选秀\n' +
    '主条目：NBA选秀\n' +
    'NBA选秀是一年一度的盛事，于每年的6月底在纽约的麦迪逊花园广场举行。参加选秀的球员必需年满19岁，美国本土球员还必需满足高中毕业至少一年的要求。（2006年选秀前可以允许高中生参与选秀，因此德怀特·霍华德成为最后一个高中新秀状元）。至2018/19赛季，仅有10名外籍球员获选状元，其中包括中国的姚明（科尔-安东尼·唐斯、阿基姆·奥拉朱旺、蒂姆·邓肯、凯里·欧文均拥有美国籍），他和意大利球员安德里亚·巴格纳尼（2006年）是仅有的两位在选秀之前没有经历过任何美国篮球体系训练的状元。\n' +
    '\n' +
    '理论上所有球队每年都会分得两个选秀权，但实际上由于选秀权可以用来交换，往往会造成有些球队在某年选秀大会上成为看客。\n' +
    '\n' +
    '选秀大会分两轮，每轮30个顺位。第二轮（31～60顺位）是直接按照球队在常规赛的成绩的倒序来排列的。第一轮后十六个顺位（15～30）是进入季后赛的十六支球队按照常规赛的成绩的倒序来排定；1～14顺位(又称为乐透区)则属于未能进入季后赛的十四支球队，不过并非是直接按照常规赛成绩来排列，而是通过抽签决定顺位的归属。也就是说，没有进入季后赛的十四支球队都有机会获得前三号选秀权，只是成绩较差的球队比较好者赢取的几率大。根据规则，常规赛成绩最差的球队将有25%的几率获得头号选秀权（即所谓的状元签），成绩倒数第二的球队有19.9%的几率，而十四队中成绩最好的球队获取的几率仅有0.5%[41]。前三号的选秀权的抽签通常在每年的5月进行。\n' +
    '\n' +
    '球队在选中新秀之后可以选择和他签约，亦可以放弃或者直接交换出去，如果新秀遭到放弃，他将成为自由球员（非受限制），如果是交易的话还没获得合同的新秀与选秀权一样是工资价值为零的。球队与新秀签约后，60天内不能把他交易出去。如果新秀不愿意与选中他的球队签约，而球队又不同意交易或放弃他的话，该新秀在一年内将不能和其它NBA球队签约，挑选他的球队保留有一年的签约权；该新秀还可以参加下一年的选秀。\n' +
    '\n' +
    '篮球名人堂\n' +
    'NBA球员可以获得的最高荣誉就是入选篮球名人堂，全称奈史密斯篮球名人纪念堂（Naismith Memorial Basketball Hall of Fame）。不过名人堂并非专门为NBA而设，而是表彰全世界为篮球事业做出卓越贡献的个人和团队，包括篮球运动员、篮球教练、篮球裁判、篮球队伍以及在其它方面为篮球奉献的人。2007年入选名人堂的NBA名人是曾10次带领球队夺得NBA总冠军、前任洛杉矶湖人队及芝加哥公牛队主教练菲尔·杰克逊[42]。\n' +
    '\n' +
    '场地规格\n' +
    'NBA的比赛场地规格为：长94呎（28.65米），宽50呎（15.24米）。\n' +
    '\n' +
    'NBA的篮筐上缘离地板距离为：10呎（3.05米）；篮筐直径为：18吋（46公分），篮网长度为：16吋（41公分）。\n' +
    '\n' +
    'NBA的篮板大小为：长6呎（1.83米），宽3.5呎（1.07米），厚3.5吋（3.81公分）；篮板上缘离地板13呎（3.96米）；篮板的内部矩形长24吋（61公分），宽18吋（46公分）。\n' +
    '\n' +
    'NBA的三分线离篮筐距离为：弧顶区域23呎9吋（7.24米），底角区域为22呎（6.71米）。\n' +
    '\n' +
    'NBA的比赛用球周长为：29.5吋（75公分），重量为：22盎司（624公克）。\n' +
    '\n' +
    'NBA领导人[43]\n' +
    '莫里斯·波杜夫（1946－63年，主席），NBA（BAA）第一任主席。出生于俄罗斯的波杜夫是一名律师，同时也是一名精明的商人和出色的谈判专家。1946年，他与一些冰球队的老板商议并成立了美国篮球协会（BAA），被推选为主席；同时他还是当时美国冰球联盟（AHL）的主席。1949年，波杜夫成功说服竞争对手国家篮球联盟（NBL），促成两个篮球组织的合并，建立了新的国家篮球协会（NBA）；1954年，波杜夫推出了一项足以改变篮球历史的措施——24秒钟装置，为NBA后来的成功埋下了坚实的基础。NBA为表扬波杜夫的贡献，把最有价值球员奖杯命名为莫里斯·波杜夫杯[11]。\n' +
    '詹姆斯·沃尔特·肯尼迪（1963－67年，主席，1967－75年，总裁）,1963年上任，是NBA第一位公选的领导人。在成为NBA主席之前肯尼迪还担任过康涅狄格州斯坦福德的市长。肯尼迪在任期间得到NBA老板的全力支持，1971年他被赋予极大的权力，成为当时全美最有统治力的职业体育联盟领导人；而他也极力扩充NBA的规模，在他离任的时候NBA球队已经由当初的9支扩大到18支。以他命名的沃尔特·肯尼迪公民奖是用来表扬热心慈善公益活动的球员或教练。\n' +
    '拉里·奥布赖恩（1975－84年，总裁），NBA第三任总裁。与其他三位总裁乃至所有体育领导人相比，奥布莱恩拥有极不寻常的背景，他曾担任美国民主党主席和美国邮政局主席，并长期作为已故美国总统约翰·肯尼迪的顾问。奥布莱恩上任后的第一件大事就是成功兼并ABA（美国篮球协会），解决了由于两联盟的恶性竞争而导致的工资飞涨、票房下降等问题，当中包括非常棘手的“奥斯卡·罗伯特森球衣”事件。1983年，奥布莱恩与NBA球员工会达成的新的劳资协议，被认为是NBA联盟发展的蓝图和基础；而他对NBA规则的改革的最大贡献在于1979年设立的三分线。奥布莱恩离任后不久，NBA把总冠军奖杯命名为拉里·奥布赖恩冠军奖杯[44]。\n' +
    '大卫·斯特恩（1984－2014年，总裁），1984年上任，是目前在位时间最长的NBA总裁。斯特恩把NBA的商业利益最大化，并成功把NBA推向全球市场，使NBA成为一个具有国际性影响力的体育联盟。\n' +
    '萧华（2014年至今，总裁），2014年上任，2014年2月1日斯特恩卸任NBA总裁，席尔瓦将接替这项职务。\n' +
    '电视转播\n' +
    '美国广播公司（ABC）、特纳电视网（TNT）以及ESPN是现今NBA在美国本土的电视合作伙伴，他们于2002年与NBA签订了为期6年价值46亿美元的合同[45]，2007年又将合约以8年74亿美元的价格延长至2016年。2014年10月6日, NBA宣布与ESPN、美国广播公司（ABC）以及特纳电视网（TNT）签订价值达到9年240亿美元(27亿/年)的续约合同，新合同从2016-17赛季开始生效一直延续到2024-25赛季，[46]这也使得NBA拥有世界上第二昂贵的电视转播合同, 仅次于NFL并同英超新电视转播合同（2016-17赛季到2018-19赛季）的年度价格相当。[47]\n' +
    '\n' +
    '联盟年度日历当中最重要的比赛包括开幕赛，圣诞节比赛，NBA全明星周末和总决赛。对于那些全国直播的常规赛，TNT安排在周四晚上，ESPN安排在周三和周五晚上, ABC的首两场直播会安排在圣诞节, 之后会从一月份开始在周六黄金时段（2015-16赛季开始）和周日下午进行常规性播出。季后赛所有比赛都会全国直播, 在前两轮ABC通常会安排在周末下午（外加1-2场周六黄金时段），ESPN通常安排在周四和周五，TNT转播剩余的比赛, 另外NBA TV也有少量直播。TNT和ESPN每年轮流转播东西部决赛，比如2016年东部决赛为ESPN，西部决赛为TNT，而2017年东部决赛变成TNT，西部决赛为ESPN。总决赛全部由ABC直播（ESPN制作）, 国际转播中不会显示ABC, 因为其使用的是NBA TV International的信号。三家电视网播出的NBA相关节目包括赛后节目Inside the NBA（TNT，每周四晚播出，查尔斯·巴克利、肯尼·史密斯担任嘉宾），赛前节目NBA Countdown（ABC和ESPN），NBA Wednsday（ESPN）、NBA Friday（ESPN）、NBA Saturday Primetime（ABC）、NBA Sunday Showcase（ABC）和NBATV的众多节目等。\n' +
    '\n' +
    '30支NBA球队各有地区收费电视网络合作伙伴，可在季前热身赛，常规赛82场比赛以及季后赛第一轮替当地观众直播赛事，只要不是由美国广播公司所直播赛赛事（不包括位处加拿大的多伦多猛龙），地区电视网络可以选择是否直播ESPN或TNT全国直播之赛事。\n' +
    '\n' +
    'NBA拥有自己的电视台NBA TV和NBA TV Canada, 另外加拿大的Sportsnet和TSN也拥有NBA常规赛的转播权，另外在市场外体育套餐方面（Out-of-market sports package）有NBA League Pass和NBA League Pass International。\n' +
    '\n' +
    '中国大陆可通过腾讯视频（全场次直播，大部分场次付费）、咪咕视频（全场次付费）在线观看NBA赛事；电视端则由央视和多家地方台体育频道（部分场次，免费）以及SiTV劲爆体育频道（部分场次，付费）转播；IPTV端由百视通提供转播。香港地区现时由ViuTV播放。\n' +
    '\n' +
    '中华民国（台湾）可透过纬来体育台（第四台）、Eleven Sports（第四台）、ELTA爱尔达电视台（中华电信MOD）进行观看。在移动设备部分以及网络直播也有Line today提供部分场次免费转播。\n' +
    '\n' +
    'NBA相关的游戏及娱乐\n' +
    'NBA很多球队都拥有自己的吉祥物，吉祥物一般出自队名或者当地的珍稀动物。每当比赛暂停时间，吉祥物就会出来场地中央表演，表演的花样很多，包括超高难度的花式灌篮（当然有弹簧垫助跳），另外还有美女啦啦队的表演。\n' +
    '\n' +
    'EA公司开发的NBA Live以及世嘉开发（后转为Take-Two）的NBA 2K是目前较受欢迎的NBA游戏，不过NBA 2K直到2K9方才推出PC版,而到2K12时则推出了手机版。 另外还有NBA篮球经理、NBA Street等。\n' +
    '\n' +
    '争议\n' +
    '参见：反对逃犯条例修订草案运动、对于中国共产党介入反对逃犯条例修订草案运动的质疑和达雷尔·莫雷涉港言论争议\n' +
    '2019年10月5日，NBA休斯敦火箭队时任总经理达雷尔·莫雷在Twitter发表“为自由而战，与香港同在（Fight for freedom, Stand with Hong Kong）”的图文，被解读为声援反送中运动。中国中央电视台体育频道于10月6日指控前述言论“不当”，当日起暂停涉及火箭队的赛事转播等合作交流，10月8日起暂停NBA赛事转播[48]。中国篮球协会、腾讯体育、浦发银行等宣布暂停或中止与休斯敦火箭队相关合作。[49][50]火箭队周边产品从中国大陆各电商平台下架。[51][52]\n' +
    '\n' +
    '10月7日，NBA首席传播官声明莫雷的观点“深深冒犯”、“令人遗憾”，支持个人表达意见，而在中文声明中称对“莫雷发表的不当言论极其失望”，严重伤害中国球迷的感情。美国两大党政治人物和传媒就此批评NBA没有维护民主价值，为商业利益向北京低头。[53][54]10月7日及8日，NBA总裁萧华表示支持言论自由[55]，“包括言论自由在内的这些价值观从联盟成立之初就存在”，向中国大陆粉丝道歉与支持言论自由并不冲突[52]。10月18日，萧华表示中国大陆政府曾要求他开除莫雷，被他断然拒绝，中国大陆外交部则否认此事。[56]10月24日，美国副总统迈克·彭斯在演说中论及“明星球员和经理以往都能够自由地批评美国，但是遇到中国人民的权利时，却默不作声，靠拢中国共产党而压抑言论自由”，批评NBA“像是被极权政府拥有的分公司”，“有意忽视侵犯人权的行为”。[57]\n' +
    '\n' +
    '2020年10月10日，中国中央电视台复播NBA赛事，赞扬NBA持续表达善意和支援中国大陆抗击2019冠状病毒病等努力。[58][59]10月16日莫雷离职火箭队，中央电视台提及“付出代价”，寄望莫雷“一路走好”。[60]11月2日，莫雷成为费城76人队篮球事务总裁，在受访时指出，事件爆发后曾担心家人安危，也一度认为自己的NBA生涯就此结束，但他并不后悔支持香港示威者的决定。[61]'
});

